@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");

.header-section {
    margin: 1rem;

    border-radius: 0.3rem;
    border-style: solid;
    border-color: #5e6973;

    padding: 0.2rem;
    background-color: #f9cb76;
    display: grid;

    grid-template-columns: 5fr repeat(4, 1fr);
}

.header-form {
    grid-column: 1;
    display: grid;
    grid-template-columns: 5fr 1fr;
    gap: 0.5rem;
}

.header-user {
    margin: 0.3rem 0.3rem 0.3rem 0.6rem;
    border-radius: 1rem;
    border-style: solid;
    border-color: #f9cb76;

    background-color: #f5f5e0;

    align-self: center;
    font: 200 1.5rem "Open Sans", sans-serif;

    text-align: center;
}

.header-join {
    border-radius: 0.5rem;
    border-style: solid;
    border-color: #f9cb76;

    padding: 0.4rem;
    background-color: #966932;

    justify-self: left;
    align-self: center;
    font: 300 1.5rem "Open Sans", sans-serif;

    color: #fcfcfc;
}

.friend {
    grid-column: 2;

    border-radius: 0.5rem;
    border-style: solid;
    border-color: #f9cb76;

    padding: 0.5rem;

    background-color: #9a9447;

    justify-self: left;
    align-self: center;

    font: 300 1.5rem "Open Sans", sans-serif;

    color: #fcfcfc;
}

.group {
    grid-column: 3;

    border-radius: 0.5rem;
    border-style: solid;
    border-color: #f9cb76;

    padding: 0.5rem;

    background-color: #9a9447;

    justify-self: left;
    align-self: center;
    font: 300 1.5rem "Open Sans", sans-serif;

    color: #ebebeb;
}

.logout {
    grid-column: 4;

    border-radius: 0.5rem;
    border-style: solid;
    border-color: #f9cb76;
    padding: 0.5rem;

    background-color: #b3210e;

    justify-self: left;
    align-self: center;
    font: 300 1.5rem "Open Sans", sans-serif;
    color: #ebebeb;
}
