@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.login-container {
    background-color: #e6eed6;
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1.5fr;
}

.login-form {
    border-radius: 0.5em;
    background-color: #d19a19;
    padding: 2em;

    display: grid;
    grid-area: 2/2;
    grid-template-rows: 2fr 1fr;
    grid-gap: 1em;
}

.login-username {
    border-radius: 0.5em;
    background-color: #afcf65;
    padding: 1em;
    display: grid;
    grid-template-rows: 2fr 1.5fr;
}

.login-username > label {
    font-family: "Akaya Kanadaka", cursive;
    font-size: 2.5em;
    justify-self: center;
    -webkit-align-self: center;
            align-self: center;
}

.login-username > input {
    font-family: "Akaya Kanadaka", cursive;
    font-size: 1.5em;
    -webkit-align-self: center;
            align-self: center;
    padding: 0.2em;
    text-align: center;
}

.login-join {
    display: grid;
}

.login-join > input {
    border: 1px solid #a72608;
    border-radius: 0.2em;
    padding: 0.2em 1em 0.2em 1em;

    background-color: #a72608;
    justify-self: center;
    -webkit-align-self: center;
            align-self: center;

    font-family: "Crimson Text", serif;
    font-size: 1.6em;
    color: #fae2aa;
}

.header-section {
    margin: 1rem;

    border-radius: 0.3rem;
    border-style: solid;
    border-color: #5e6973;

    padding: 0.2rem;
    background-color: #f9cb76;
    display: grid;

    grid-template-columns: 5fr repeat(4, 1fr);
}

.header-form {
    grid-column: 1;
    display: grid;
    grid-template-columns: 5fr 1fr;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

.header-user {
    margin: 0.3rem 0.3rem 0.3rem 0.6rem;
    border-radius: 1rem;
    border-style: solid;
    border-color: #f9cb76;

    background-color: #f5f5e0;

    -webkit-align-self: center;

            align-self: center;
    font: 200 1.5rem "Open Sans", sans-serif;

    text-align: center;
}

.header-join {
    border-radius: 0.5rem;
    border-style: solid;
    border-color: #f9cb76;

    padding: 0.4rem;
    background-color: #966932;

    justify-self: left;
    -webkit-align-self: center;
            align-self: center;
    font: 300 1.5rem "Open Sans", sans-serif;

    color: #fcfcfc;
}

.friend {
    grid-column: 2;

    border-radius: 0.5rem;
    border-style: solid;
    border-color: #f9cb76;

    padding: 0.5rem;

    background-color: #9a9447;

    justify-self: left;
    align-self: center;

    font: 300 1.5rem "Open Sans", sans-serif;

    color: #fcfcfc;
}

.group {
    grid-column: 3;

    border-radius: 0.5rem;
    border-style: solid;
    border-color: #f9cb76;

    padding: 0.5rem;

    background-color: #9a9447;

    justify-self: left;
    align-self: center;
    font: 300 1.5rem "Open Sans", sans-serif;

    color: #ebebeb;
}

.logout {
    grid-column: 4;

    border-radius: 0.5rem;
    border-style: solid;
    border-color: #f9cb76;
    padding: 0.5rem;

    background-color: #b3210e;

    justify-self: left;
    align-self: center;
    font: 300 1.5rem "Open Sans", sans-serif;
    color: #ebebeb;
}

.chatlist {
    grid-area: 1 / 1 / 1 / 1;
    background-color: #fdfdff;
}

.chat-container {
    height: 100vh;
    width: 100vw;
    background-color: #5e6973;
    display: grid;
    grid-template-rows: 1fr 8fr;
    grid-gap: 0.7rem;
    gap: 0.7rem;
}

.section-2 {
    display: grid;
    grid-template-rows: 7fr 1fr;
    grid-template-columns: 1fr 3fr;
    background-color: #fdfdff;
}

.personal-message-self-text {
    grid-column: 2 / span 2;

    border-style: solid;
    border-radius: 0.5rem 0rem 0.5rem 0rem;

    padding-left: 1rem;
    padding-right: 1rem;

    display: grid;
    justify-self: right;
    word-wrap: break-word;

    text-align: left;
}



.group-message-self-text {
    grid-column: 2 / span 2;

    border-style: solid;
    border-radius: 0.5rem 0rem 0.5rem 0rem;

    padding-left: 1rem;
    padding-right: 1rem;

    display: grid;
    justify-self: right;
    word-wrap: break-word;

    text-align: left;
}

.display {
    grid-area: 1/2/1/2;
    display: grid;
    grid-template-rows: minmax(0, 1fr) minmax(0, 9fr);
    grid-gap: 1rem;
    gap: 1rem;
}

.display-header {
    grid-area: 1/1;
    display: grid;
    border-style: solid;
    border-radius: 0.6rem;
    font: 200 1.3rem "Open Sans", sans-serif;
}

.display-header > h {
    display: grid;
    justify-self: center;
    align-self: center;
}

.overflow {
    grid-area: 2/1;
    max-height: 64vh;
    overflow-y: scroll;
    scrollbar-width: none;
}

.display-messages {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    grid-auto-rows: minmax(0, 12.5%);
}

.sendbar {
    grid-area: 2 / 2 / 2 / 2;
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-gap: 0.2rem;
    gap: 0.2rem;
}

.sendbar-option-button {
    border-radius: 1rem;
    border-style: solid;

    justify-self: right;
    -webkit-align-self: center;
            align-self: center;

    font: 300 1.2rem "Open Sans", sans-serif;
}

.sendbar-form {
    display: grid;
    grid-template-columns: 4fr 1fr;
    grid-gap: 0.2rem;
    gap: 0.2rem;
}

.sendbar-text {
    border-radius: 0.5rem;
    border-style: solid;

    font: 300 1.2rem "Open Sans", sans-serif;
    padding: 0.05rem 0.05rem 0.05rem 0.3rem;
    -webkit-align-self: center;
            align-self: center;
    text-align: left;
}

.sendbar-image {
    border-radius: 0.7rem;
    border-style: solid;

    justify-self: center;
    -webkit-align-self: center;
            align-self: center;
    padding: 0.05rem 0.05rem 0.05rem 0.3rem;

    font: 300 1.2rem "Open Sans", sans-serif;
}

.sendbar-image > input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.sendbar-send-button {
    border-radius: 1rem;
    border-style: solid;

    justify-self: left;
    -webkit-align-self: center;
            align-self: center;

    font: 300 1.2rem "Open Sans", sans-serif;
}

