.personal-message-self-text {
    grid-column: 2 / span 2;

    border-style: solid;
    border-radius: 0.5rem 0rem 0.5rem 0rem;

    padding-left: 1rem;
    padding-right: 1rem;

    display: grid;
    justify-self: right;
    word-wrap: break-word;

    text-align: left;
}



