.sendbar {
    grid-area: 2 / 2 / 2 / 2;
    display: grid;
    grid-template-columns: 1fr 4fr;
    gap: 0.2rem;
}

.sendbar-option-button {
    border-radius: 1rem;
    border-style: solid;

    justify-self: right;
    align-self: center;

    font: 300 1.2rem "Open Sans", sans-serif;
}

.sendbar-form {
    display: grid;
    grid-template-columns: 4fr 1fr;
    gap: 0.2rem;
}

.sendbar-text {
    border-radius: 0.5rem;
    border-style: solid;

    font: 300 1.2rem "Open Sans", sans-serif;
    padding: 0.05rem 0.05rem 0.05rem 0.3rem;
    align-self: center;
    text-align: left;
}

.sendbar-image {
    border-radius: 0.7rem;
    border-style: solid;

    justify-self: center;
    align-self: center;
    padding: 0.05rem 0.05rem 0.05rem 0.3rem;

    font: 300 1.2rem "Open Sans", sans-serif;
}

.sendbar-image > input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.sendbar-send-button {
    border-radius: 1rem;
    border-style: solid;

    justify-self: left;
    align-self: center;

    font: 300 1.2rem "Open Sans", sans-serif;
}
