.display {
    grid-area: 1/2/1/2;
    display: grid;
    grid-template-rows: minmax(0, 1fr) minmax(0, 9fr);
    gap: 1rem;
}

.display-header {
    grid-area: 1/1;
    display: grid;
    border-style: solid;
    border-radius: 0.6rem;
    font: 200 1.3rem "Open Sans", sans-serif;
}

.display-header > h {
    display: grid;
    justify-self: center;
    align-self: center;
}

.overflow {
    grid-area: 2/1;
    max-height: 64vh;
    overflow-y: scroll;
    scrollbar-width: none;
}

.display-messages {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    grid-auto-rows: minmax(0, 12.5%);
}
